import request from "@/utils/request";

export function checklist_list_get(token) {
  return request({
    url: "/checklist/list",
    method: "get",
    params: { token }
  });
}

export function checklist_list_post(token, data) {
  return request({
    url: "/checklist/list",
    method: "post",
    params: { token },
    data
  });
}

export function checklist_options_post(token, data) {
  return request({
    url: "/checklist/options",
    method: "post",
    params: { token },
    data
  });
}

export function checklist_options_put(token, data) {
  return request({
    url: "/checklist/options",
    method: "put",
    params: { token },
    data
  });
}

export function checklist_options_delete(token, data) {
  return request({
    url: "/checklist/options",
    method: "delete",
    params: { token },
    data
  });
}
