<template>
  <div class="add-container">
    <el-card shadow="always" class="add-box-card">
      <el-form :model="taskInfo" :rules="rules" ref="ruleForm">
        <el-form-item label="待办事项名" label-width="100px" prop="title">
          <el-input
            v-model="taskInfo.title"
            auto-complete="off"
            placeholder="请输入待办事项名"
            maxlength="30"
            show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="待办事项内容" label-width="100px">
          <el-input
            v-model="taskInfo.content"
            type="textarea"
            maxlength="300"
            placeholder="请输入待办事项内容"
            rows="5"
            show-word-limit
          />
        </el-form-item>
        <el-form-item label="所属清单" label-width="100px" prop="checklist_id">
          <el-select
            v-model="taskInfo.checklist_id"
            placeholder="请选择所属清单"
            style="width: 100%;"
          >
            <el-option
              v-for="item in checklist"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="所属日期" label-width="100px">
          <el-date-picker
            v-model="taskInfo.add_time"
            type="date"
            placeholder="选择日期"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            style="width: 100%;"
          />
        </el-form-item>
        <el-form-item label="执行时间" label-width="100px">
          <el-time-select
            v-model="taskInfo.task_time"
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30'
            }"
            placeholder="选择时间"
            style="width: 100%;"
          />
        </el-form-item>
        <el-form-item label="事项循环" label-width="100px">
          <el-button-group>
            <el-button
              v-for="(item, index) in [
                '一',
                '二',
                '三',
                '四',
                '五',
                '六',
                '日'
              ]"
              :key="index"
              :type="cycle_trans['index' + (index + 1)] ? 'primary' : ''"
              @click="toggle_cycle(index + 1)"
            >
              星期{{ item }}
            </el-button>
          </el-button-group>
        </el-form-item>
        <el-form-item>
          <div class="add-btn-group">
            <el-button
              type="info"
              class="add-btn"
              @click="resetForm('ruleForm')"
            >
              重置
            </el-button>
            <el-button
              type="primary"
              class="add-btn"
              @click="onSubmit('ruleForm')"
            >
              添加
            </el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { addTask } from "@/api/task";
import { checklist_list_get } from "@/api/checklist";

export default {
  data() {
    return {
      taskInfo: {},
      rules: {
        title: [
          { required: true, message: "请输入待办事项名称", trigger: "blur" }
        ],
        checklist_id: [
          { required: true, message: "请选择所属清单", trigger: "change" }
        ]
      },
      localCycle: [],
      checklist: []
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      checklist_list_get(this.$store.getters.token).then(response => {
        this.checklist = response.data;
      });
    },
    cancel() {
      this.taskInfo = {};
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.taskInfo.cycle = this.localCycle.join("#");
          addTask(this.$store.getters.token, this.taskInfo).then(() => {
            this.cancel();
            this.$notify({
              title: "系统通知",
              message: "添加待办事项成功",
              type: "success"
            });
          });
        } else {
          return false;
        }
      });
    },
    toggle_cycle(index) {
      let cycle_index = this.localCycle.indexOf(index + "");
      if (cycle_index !== -1) {
        this.localCycle.splice(cycle_index, 1);
      } else {
        if (this.localCycle.indexOf("0") !== -1) {
          this.localCycle = [];
        }
        this.localCycle.push(index + "");
        this.localCycle.sort();
      }
    }
  },
  computed: {
    cycle_trans() {
      let cycle = {};
      // 当循环只有一个数的时候
      if (this.localCycle.length === 1) {
        // 当只有一天循环的时候
        for (let i = 1; i < 8; i++) {
          cycle["index" + i] = i + "" === this.localCycle[0];
        }
      } else {
        // 当有多天循环的时候
        for (let i = 1; i < 8; i++) {
          cycle["index" + i] = this.localCycle.indexOf(i + "") !== -1;
        }
      }

      return cycle;
    }
  }
};
</script>

