import request from "@/utils/request";

export function getTask(token, data) {
  return request({
    url: "/task",
    method: "post",
    headers: { "content-type": "application/x-www-form-urlencoded" },
    params: { token },
    data: data
  });
}

export function addTask(token, data) {
  return request({
    url: "/task",
    method: "put",
    headers: { "content-type": "application/x-www-form-urlencoded" },
    data: data,
    params: { token }
  });
}

export function deleteTask(token, taskid) {
  return request({
    url: `/task/${taskid}`,
    method: "delete",
    headers: { "content-type": "application/x-www-form-urlencoded" },
    params: { token }
  });
}

export function changeTask(token, taskid, data) {
  return request({
    url: `/task/${taskid}`,
    method: "post",
    headers: { "content-type": "application/x-www-form-urlencoded" },
    data: data,
    params: { token }
  });
}

